.left-bar {
    width: 50px;
    height: 84.9vh;
    background-color: #323232;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 40px;
    box-shadow: 2px 0 4px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    
  }
  
  .icon-container {
    display: flex;
    flex-direction: column;
    gap: 40px; /* Space between icons */
  }
  
  .icon {
    color: white;
    cursor: pointer;
    transition: transform 0.2s, color 0.2s;
  }
  
  .icon:hover {
    color: #00ff00; /* Highlight color on hover */
    transform: scale(1.2); /* Slight zoom effect */
  }
